<template>
  <header class="header">
    <div class="header-logo"></div>
    <event-filter></event-filter>
  </header>
</template>

<style>
  @import "variables.css";

  .header {
    width: 100%;
    background: var(--headerBgColor);
    height: var(--headerHeightLg);
    padding-top: 10px;
    box-sizing: border-box;
    top: 0;
    z-index: 999999;
  }

  @media (--header-medium) {
    .header {
      height: var(--headerHeightMd);
    }
  }

  .header-logo {
    background: url("../assets/img/logo.png") no-repeat;
    width: 366px;
    height: 13px;
    margin-left: 1.5rem;
    float: left;
    margin-top: 5px;

    @media (max-width: 1180px) {
      margin-left: auto;
      margin-right: auto;
      float: none;
      text-align: center;
    }

    @media (max-width: 480px) {
      background: url("../assets/img/logo-md.png") no-repeat;
      width: 217px;
    }
  }

</style>

<script>
  import EventFilter from './EventFilter'

  export default {
    components: {
      EventFilter
    }
  }
</script>



