import Vue from 'vue'
import Router from 'vue-router'
import fecha from 'fecha'
import EventForm from '../components/EventForm'
import EventView from '../components/EventView'
import LoginForm from '../components/LoginForm'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      redirect: function () {
        return '/'+fecha.format(new Date(), 'YYYY-MM-DD');
      }
    },
    {
      path: '/:date',
      name: 'date'
    },
    {
      path: '/:date/login',
      component: LoginForm,
      name: 'login'
    },
    {
      path: '/:date/add',
      component: EventForm,
      name: 'addEvent'
    },
    {
      path: '/:date/view/:id',
      component: EventView,
      name: 'viewEvent'
    },
    {
      path: '/:date/edit/:id',
      component: EventForm,
      name: 'editEvent'
    }
  ]
})



