<template>
  <button @click="switchMode()">{{buttonSwitchModeTitle}}</button></div>
</template>

<style>
  .calendar-wrapper .switch button {
    background: none;
    border: 0;
    color: #16407d !important;
    font-size: 1.3rem;
    text-decoration: underline;
    float: left;;
  }
</style>

<script>
  export default {
    methods: {
      switchMode: function() {
        this.$store.dispatch('switchMode')
      }
    },
    computed: {
      buttonSwitchModeTitle: function(){
        return this.$store.state.display_mode_compact ? 'Switch to Normal Mode' : 'Switch to Compact Mode'
      }
    }
  }
</script>



