      <form id="form-login" name="form-login" autocomplete="off" class="forms">
        <fieldset>
          <legend>Informasjon</legend>
          <label><span name="localised">Tittel</span></label>
          <input type="text" name="login_name" class="u-full-width">
          <div class="clearfix">
            <div class="col-1">
              <label><span name="localised">Kategori</span></label>
              <input type="text" name="login_name" placeholder="Velg kategori" class="u-full-width">
            </div>
            <div class="col-2">
              <label><span name="localised">Organisasjon</span></label>
              <input type="text" name="login_name" placeholder="Velg organisasjon" class="u-full-width">
