  function eventHyhenator(v) {
    v.category.name = eventTitles[v.category.name.toLowerCase()] || v.category.name;
    return v;
  }

  function setEventTheme(v) {
    v.event_theme = v.category.parent_id == 3 ? 'grey-blue' : (v.category.parent_id == 2 ? 'blue-red' : 'grey-black');
    return v;
  }

  function getEvents() {
    if (!loadedEvents) {
      loadedEvents = loadEvents();
      return loadedEvents;
    }

    return getCachedEvents();
  }

  function loadEvents() {
    return Promise.resolve(EventGenerator(Categories, Regions, Levels, Organizations))
      .then(function (data) {
        return _.chain(data).map(eventHyhenator).map(setEventTheme).value();
      })
      .then(setCachedEvents);
  }

  function setCachedEvents(data) {
    loadedEvents = data;
    return loadedEvents;
  }

  function getCachedEvents() {
    return Promise.resolve(loadedEvents);
  }

