<template>
  <router-link :to="{ name: 'viewEvent', params: Object.assign({}, routeParams, {id: event.id}) }" class="event-item">
    <div class="event-box" v-bind:class="[event.event_theme]">
      <div class="event-item--img">
        <div class="middle-helper"></div>
        <img v-if="brandImage" v-bind:src="brandImage" v-bind:alt="event.category">
      </div>
      <div class="event-item--type"><span>{{event.category.name}}</span></div>
    </div>
    <div class="event-description">{{event.title}}</div>
  </router-link>
</template>

<style>
  @import "variables.css";

  .event-item {
    margin: 0 1rem 1.5rem;
    width: 16rem;
    text-decoration: none;
  }

  .event-item .event-box {
    display: table;
    table-layout: fixed;
    height: 4.2rem;
    width: 16rem;
    margin-bottom: .5rem;
    cursor: pointer;
  }

  .event-item .event-box.lowercase span {
    text-transform: none;
    font-weight: 500
  }

  .event-item .event-box.blue-red .event-item--img {
    background: #16407d;
    border: 0
  }

  .event-item .event-box.blue-red .event-item--type {
    background: #e73e42;
    color: #fff;
    border: 0
  }

  .event-item .event-box .event-item--img {
    display: table-cell;
    width: 4rem;
    position: relative;
    background: #9b9b9b;
    z-index: 1;
    border: 2px solid #9b9b9b;
  }

  .event-item .event-box .event-item--img img {
    max-height: 100%;
    max-width: 100%;
    width: auto;
    height: auto;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto
  }

  .event-item .event-box .event-item--type {
    display: table-cell;
    vertical-align: middle;
    text-transform: uppercase;
    color: #16407d;
    font-size: 1.1rem;
    font-weight: bold;
    border: 2px solid #9b9b9b;
    border-left: 0;
  }

  .event-item .event-box .event-item--type span {
    padding: 0 .7rem;
    display: block
  }

  .event-item .event-description {
    color: #000;
    font-size: 1.2rem
  }

  /* Vertical layout for timeline */
  @media (--timeline-large) {
    .event-item {
      flex: 0 1 auto;
      align-self: auto;
      order: 0;
    }
  }
  @media (--timeline-small) {
    .event-item {
      margin: 0 2rem 2rem 0rem;
      display: inline-block;
      vertical-align: top;
    }
  }

  .compact-mode .event-item {
    margin-bottom: 2rem;
    width: 7rem;
    display: inline-block;
    vertical-align: top;
  }

  .compact-mode .event-item .event-description {
    line-height: 1.1;
    font-size: 1.1rem;
    font-weight: 500;
    color: #16407d;
    word-wrap: break-word;

  }

  .compact-mode .event-item .event-box {
    width: 6rem;
  }

  .compact-mode .event-item .event-box .event-item--type {
    display: none
  }

  @media (--timeline-small) {
    .compact-mode .event-item:last-child {
      margin-bottom: 4rem;
    }
  }
</style>

<script>
  import _ from 'lodash';
  import { mapState } from 'vuex';


  export default {
    props: ['event'],
    computed: {
      brandImage: function(){
        return _.get(this.event, 'organization.logo.href', false)
      },
      ...mapState({
        routeParams (state){
          return state.route.params;
        }
      })
    }
  }
</script>




