<template>
  <footer>
    <footer-calendar-button></footer-calendar-button>
    <router-link :to="{ name: 'addEvent', params: routeParams}"><i class="icon icon-plus-squared"></i></router-link>
    <router-link :to="{ name: 'login', params: routeParams}"><i class="icon icon-logout"></i></router-link>
  </footer>
</template>

<style>
  @import "variables.css";

  footer {
    display: block;
    box-sizing: border-box;
    text-align: center;
    z-index: 999;
  }

  @media (--timeline-large) {
    footer {
      padding: 1.05rem 0 0 2rem;
      position: fixed;
      bottom: 0;
      width: 0;
    }
  }

  @media (--header-large) {
    footer {
      top: var(--headerHeightLg);
    }
  }

  @media (--header-medium) {
    footer {
      top: var(--headerHeightMd);
    }
  }

  @media (--timeline-small) {
    footer {
      position: static;
      background: #16407d;
      height: var(--footerHeightSm);
      width: 100%;
      text-align: left;
    }
  }

  footer .icon {
    display: inline-block;
    color: #16407d;
    background: #fff;
    z-index: 900;
    border: 1px solid #16407d;
    font-size: 2.2rem;
    padding: .3rem;
    cursor: pointer;
  }

  @media (--timeline-large) {
    footer .icon {
      &.icon-plus-squared {
         margin-top: 2rem;
      }

      &.icon-logout {
         position: fixed;
         bottom: 2rem;
         left: 2rem;
      }
    }
  }

  @media (--timeline-small) {
    footer .icon {
      box-shadow: none;


      &.icon-logout {
         float: right;
      }
    }
  }
</style>

<script>
  import { mapState } from 'vuex'
  import FooterCalendarButton from './FooterCalendarButton'

  export default {
    components: {
      FooterCalendarButton
    },
    computed: {
      ...mapState({
        routeParams (state){
          return state.route.params;
        }
      })
    }

  }
</script>



