<template>
  <section class="timeline" v-bind:class="{ 'compact-mode': classCompactMode }">
    <component v-for="event in events" v-bind:is="currentView(event.type)" v-bind:event="event.data"></component>

    <!-- timeline-section v-bind:section="section" v-for="section in sections"></timeline-section -->
  </section>
</template>

<style>
  @import "variables.css";

  .timeline {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-content: flex-start;
    align-items: flex-start;

    height:calc(100vh - var(--headerHeightLg));
    padding: var(--timelineTopPadding) 0 0 3rem;
    box-sizing: border-box;
    overflow-x: auto;
    overflow-y: hidden;
    background: linear-gradient(to bottom, #4a90e2 0%, #4a90e2 100%);
    background-size: 3px 3px;
    background-repeat: repeat-x;
    background-position: 0  3rem;
  }

  @media (--header-medium) {
    .timeline {
      height:calc(100vh - var(--headerHeightMd));
    }
  }

  @media (--header-small) {
    .timeline {
      height:calc(100vh - (var(--headerHeightSm) + var(--footerHeightSm)));
    }
  }

  /* Vertical layout for timeline */
  @media (--timeline-small) {
    .timeline {
      display: block;
      padding: 2rem 0 2em 4.2rem;
      overflow-x: hidden;
      overflow-y: auto;
      background-repeat: repeat-y;
      background-position: 2rem 0;
    }
  }
</style>

<script>
  import TimelineEvent from './TimelineEvent'
  import TimelineSeparator from './TimelineSeparator'
  import { mapState } from 'vuex'
  import _ from 'lodash/core';
  import fecha from 'fecha';
  import weekNumber from '../helpers/week-number'

  function getSectionKey(mode, date) {
    switch (mode){
      case 'year':
        return fecha.format(date, 'YYYY');
      case 'month':
        return fecha.format(date, 'YYYYMM');
      case 'week':
        const weekNum = weekNumber(date);
        return date.getFullYear() + '' + (weekNum < 10 ? '0'+weekNum : weekNum);
      default:
        return fecha.format(date, 'YYYYMMDD');
    }
  }

  export default {
    components: {
      TimelineEvent,
      TimelineSeparator
    },
    created () {
      this.$store.dispatch('loadEvents')
    },
    methods: {
      currentView: function(type){
        return type === 'separator' ? 'timeline-separator' : 'timeline-event'
      }
    },
    computed: {
      ...mapState({
        classCompactMode: "display_mode_compact",
        events (state){
          let previousEvent;
          return _.chain(state.events)
            .sortBy('start_date')
            .reduce(function (result, value) {
              if (!previousEvent) {
                result.push({
                  type: 'separator',
                  data: {
                    date: value.start_date
                  }
                });
              }
              if (previousEvent) {
                var prevKey = getSectionKey(state.timeline_mode, previousEvent.start_date);
                var currentKey = getSectionKey(state.timeline_mode, value.start_date);
                if (prevKey !== currentKey) {
                  result.push({
                    type: 'separator',
                    data: {
                      date: value.start_date
                    }
                  });
                }
              }
              result.push({
                type: 'event',
                data: value
              });
              previousEvent = value;
              return result;
            }, [])
            .value()
        }
      })
    }
  }
</script>



