import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'
import db from '../helpers/db'

Vue.use(Vuex)

const state = {
  /* day, week, month */
  display_mode_compact: false,
  timeline_mode: 'day', // day, week, month, year
  events: [],
  categories: [],
  regions: [],
  levels: [],
  organizations: []
}

const mutations = {
  SWITCH_MODE(state) {
    state.display_mode_compact = !state.display_mode_compact;
  },
  SWITCH_TIMELINE_MODE(state, data) {
    state.timeline_mode = data;
  },
  LOAD_CATEGORIES(state, data) {
    state.categories = data;
  },
  LOAD_REGIONS(state, data) {
    state.regions = data;
  },
  LOAD_ORGANIZATONS(state, data) {
    state.organizations = data;
  },
  LOAD_LEVELS(state, data) {
    state.levels = data;
  },
  LOAD_EVENTS(state, data) {
    state.events = data;
  },
  ADD_EVENT(state, data) {
    state.events.push(data);
  }
}

const actions = {
  loadCategories({commit}) {
    db.Categories.getAll().then(function(data){
      commit('LOAD_CATEGORIES', data)
    });
  },
  loadOrganizations({commit}) {
    db.Organizations.getAll().then(function(data){
      commit('LOAD_ORGANIZATONS', data)
    });
  },
  loadRegions({commit}) {
    db.Regions.getAll().then(function(data){
      commit('LOAD_REGIONS', data)
    });
  },
  loadLevels({commit}) {
    db.Levels.getAll().then(function(data){
      commit('LOAD_LEVELS', data)
    });
  },
  loadEvents({commit}) {
    db.Events.getAll().then(function(data){
      commit('LOAD_EVENTS', data)
    });
  },
  switchMode ({commit}) {
    commit('SWITCH_MODE')
  },
  switchTimelineMode ({commit}, mode) {
    commit('SWITCH_TIMELINE_MODE', mode)
  }
}


const store = new Vuex.Store({
  state,
  mutations,
  actions,
  plugins: [createPersistedState({
    paths: ['categories', 'regions', 'levels', 'organizations', 'display_mode_compact', 'timeline_mode']
  })]
})

export default store



