<template>
  <div class="modal-mask" v-bind:class="[ modalClass ]" @click="close">
    <div class="modal-wrapper">
      <div class="modal-container" @click.stop>
        <div class="modal--header">
          <div class="close modal-default-button" @click="close()"></div>
          <slot name="header"></slot>
        </div>
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<style>
  @import "variables.css";

  .modal-mask {
    position: fixed;
    z-index: 9998;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    display: table;
    transition: opacity .3s ease;
  }

  .modal-wrapper {
    display: table-cell;
    vertical-align: middle;
  }

  .modal-container {
    width: 60rem;
    margin: 0px auto;
    background-color: #fff;
    border-radius: 2px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
    transition: all .3s ease;
    font-family: Helvetica, Arial, sans-serif;
    position: relative;
    text-align: left;
  }

  .modal--header  {
    color: #fff;
    padding: 3rem 2.5rem 1rem;
    background: #ed4044;
  }

  .modal--header h3 {
    color: #fff;
    padding: 0;
    margin: 0;
    font: 400 2.4rem/1.2 'Helvetica Neue Condensed', "Roboto Condensed", sans-serif;
    text-transform: uppercase
  }

  .modal--header .close {
    position: absolute;
    z-index: 10000;
    top: 0;
    right: 0;
    line-height: 25px;
    line-height: 2.5rem;
    width: 25px;
    width: 2.5rem;
    cursor: pointer;
    text-align: center;
    font-family: Arial, sans-serif;
    background: #fff;
    background: rgba(255, 255, 255, 0.3);
    color: #000
  }

  .modal-container fieldset {
    border: 1px solid #c0c0c0;
    margin: 0 2px 1.5rem;
    padding: 0.35em 0.625em 0.75em;
  }

  .modal-container form {
    padding: 2.5rem 2rem;
    margin: 0;
  }

  .modal-container form label {
    display: block;
    line-height: 1.2;
    font-weight: 400;
    margin-bottom: 0.2rem;
  }

  .modal-container input,
  .modal-container textarea {
    height: 38px;
    height: 3.8rem;
    padding: 0.6rem 1rem;
    background-color: #fff;
    border: 1px solid #d1d1d1;
    border-radius: 2px;
    box-shadow: none;
    box-sizing: border-box;
    width: 100%;
    margin-bottom: 2rem;
  }

  .modal-container form textarea {
    height: 13rem;
  }
  .modal-container form button {
    color: #fff;
    background-color: #1eaedb;
    border-color: #1eaedb;
    border: 0;
    height: 3.8rem;
    padding: 0 3rem;
    text-transform: uppercase;
    width: 100%;
    font-weight: 600;
    font-size: 1.1rem;
  }

  .modal-container .col-1,
  .modal-container .col-2 {
    float: left;
    width: 49%;
  }

  .modal-container .col-2{
    margin-left: 2%;
  }

  .modal--content-scroller {
    overflow-y: scroll;
    height: 49rem;
  }

  .modal-default-button {
    float: right;
  }

  @media (--phone) {
    .modal-container form {
      padding: 1.5rem 1rem;
    }
    .modal-container .col-1, .modal-container .col-2 {
      float: mome;
      width: 100%;
      margin-left: 0;
    }
  }

  @media (--timeline-small) {
    .modal-container {
      width: 40rem;
    }
  }

  @media (--phone) {
    .modal-container {
      width: 31rem;
    }
  }

</style>

<script>
  export default {
    props: ['onClose', 'modalClass'],
    methods: {
      close: function () {
        this.onClose();
      }
    },
    mounted: function () {
      document.addEventListener("keydown", (e) => {
        if (this.show && e.keyCode == 27) {
          this.onClose();
        }
      });
    }
  }
</script>



