<template>
  <modal :modal-class="'login'" :on-close="close">
    <h3 slot="header">Login</h3>

    <form id="form-login" name="form-login" autocomplete="off" class="forms">
      <div class="form-loader"></div>
      <div class="form-errors">  </div>
      <label>Login navn</label>
      <input type="text" name="login_name">
      <label>Passord</label>
      <input type="password" name="password">
      <button type="submit" class="button-primary">Login</button>
    </form>

  </modal>
</template>


<style>
  @import "variables.css";

  .login .modal-container {
    width: 30rem;
    height: 33rem;
  }

  .login .modal--header {
    background: #16407d;
  }
</style>


<script>
  import Modal from './Modal'
  import router from '../router/index'

  export default {
    components: {
      Modal
    },
    methods: {
      close: function(){
        router.push({name:'date', params: this.$route.params})
      }
    }
  }
</script>



