<template>
  <header class="timeline-section__header">
    <h3 v-if="subtitle">{{subtitle}}</h3>
    <h4>{{title}}</h4>
  </header>
</template>

<style>
  @import "variables.css";

  .timeline-section__header {
    padding:2rem 0 0 2rem;
    position: relative;
  }

  .timeline-section__header:before {
    background: #fff;
    content: '';
    height: 1rem;
    width: 1rem;
    border: 2px solid #4a90e2;
    display: block;
    border-radius: 50%;
    position: absolute;
    top: 1rem;
    left: 0rem;
    z-index: 1
  }

  .timeline-section__header h3 {
    color: #5e6066;
    font-size: 1rem;
    font-weight: normal;
    margin: 0;
    padding: 0;
    position: absolute;
    top: 0;
    left: 2rem;
    white-space: nowrap;
  }

  .timeline-section__header h4 {
    font-size: 1.3rem;
    font-weight: normal;
    padding: 0;
    margin: 0;
    white-space: nowrap;
  }

  /*.timeline_section--past .section__header:before {
    border-color: #62c6f1;
  }

  .timeline_section--current .section__header:before {
    background-color: #e73e42
  }

  .timeline_section--past .section__header h4 {
    color: #5E6066;
  }*/
</style>

<script>
  import fecha from 'fecha';
  import weekNumber from '../helpers/week-number'

  function getSectionTitle(mode, date) {
    switch (mode){
      case 'year':
        return fecha.format(date, 'YYYY'); // Year
      case 'month':
        return fecha.format(date, 'MMMM'); // Month
      case 'week':
        return weekNumber(date) + ' Week'; // Week 10
      default:
        return fecha.format(date, 'MMMM Do'); // September, 26
    }
  }

  function getSectionSubtitle(mode, date) {
    switch (mode){
      case 'year':
        return false; // Nothing here
      case 'month':
        return fecha.format(date, 'YYYY'); // Year
      case 'week':
        return fecha.format(date, 'MMMM'); // September
      default:
        return weekNumber(date) + ' Week';  // Week 10
    }
  }

  export default {
    props: ['date'],
    computed: {
      title: function(){
        return getSectionTitle(this.$store.state.timeline_mode, this.date);
      },
      subtitle: function(){
        return getSectionSubtitle(this.$store.state.timeline_mode, this.date);
      }
    }
  }
</script>



