<template>
  <form class="filter-list" action="">
    <select v-model="filterByOrganization">
      <option value="">Organisasjoner</option>
      <option v-for="option in filterOrganizations" v-bind:value="option.id">{{option.name}}</option>
    </select>
    <select v-model="filterByLevel">
      <option value="">Niv</option>
      <option v-for="option in filterLevels" v-bind:value="option.id">{{option.name}}</option>
    </select>
    <select v-model="filterByRegion">
      <option value="">Regions</option>
      <option v-for="option in filterRegions" v-bind:value="option.id">{{option.name}}</option>
    </select>
    <select v-for="category in filterCategories" v-model="filterByCategory[category.id]">
      <option value="">{{category.name}}</option>
      <option v-for="option in category.options" v-bind:value="option.id">{{option.name}}</option>
    </select>
    <button>Apply</button>
  </form>
</template>
<style>
  @import "variables.css";

  .filter-list {
    float: right;
    margin-right: 10px;
    box-sizing: border-box;
    font-family: 'Helvetica Neue Condensed', "Roboto Condensed", sans-serif;
  }

  .filter-list select {
    border: 0;
    background: #16407d;
    color: #fff;
    text-transform: uppercase;
    font-size: 13px;
    width: 12rem;
    border-radius: 0;
  }

  .filter-list button {
    display: none
  }

  @media (--header-medium) {
    .filter-list {
      float: none;
      margin: 0;
      padding-top: 1rem;
      text-align: center;
      clear: both;
    }
  }

  @media (--header-small) {
    .filter-list {
      position: fixed;
      top: var(--headerHeightSm);
      /*bottom: 0;*/
      left: 0;
      right: 0;
      padding: 2rem;
      background: #16407d;
      width: 100%;
      display: none;
      overflow-y: auto;
    }

    .filter-list select {
      font-size: 16px;
      width: 100%;
      margin: .5rem 0;
      border:1px solid #a4c2ef;
    }

    .filter-toggle-checkbox:checked + .filter-toggle + .filter-list {
      display: block;
      z-index: 901;
    }

    /*
    .filter-list .chosen-container {
      display: block !important;
      margin-bottom: 1rem;
      width: 90% !important;
      margin-left: 5%;
    }
    .chosen-container-active.chosen-with-drop .chosen-single {
      border: 1px solid #fff;
      box-shadow: none;
      background: #16407d;
    }
    */
  }
</style>
<script>
  import { mapState } from 'vuex'
  import _ from 'lodash/core';

  function getOptions(data) {
    return _.map(data, (v => _.pick(v, ['id', 'name'])))
  }

  export default {
    created () {
      this.$store.dispatch('loadCategories')
      this.$store.dispatch('loadOrganizations')
      this.$store.dispatch('loadRegions')
      this.$store.dispatch('loadLevels')
    },
    data: function(){
      return {
        filterByOrganization: '',
        filterByLevel: '',
        filterByRegion: '',
        filterByCategory: {
          0: '',
          1: '',
          2: ''
        }
      }
    },
    computed: {
      ...mapState({
        filterOrganizations (state){
          return getOptions(state.organizations)
        },
        filterRegions (state){
          return getOptions(state.regions)
        },
        filterLevels (state){
          return getOptions(state.levels)
        },
        filterCategories: function(state){
          return _.map(state.categories, function(v, k){
            return {
              id: k,
              name: v.name,
              options: getOptions(v.children)
            }
          });
        }
      })
    }
  }
</script>



