<template>
  <select v-model="timelineMode">
    <option v-for="option in options" v-bind:value="option.value">
      {{ option.text }}
    </option>
  </select>
</template>

<style>
  .calendar-wrapper .period {
    float: right;
  }

  .calendar-wrapper > select {
    border: 1px solid #16407d;
    font-size: 1.3rem;
  }
</style>


<script>
  export default {
    data: function() {
      return {
        timelineMode: 'day',
        options: [
          { text: 'Day', value: 'day' },
          { text: 'Week', value: 'week' },
          { text: 'Month', value: 'month' }
//          { text: 'Year', value: 'year' }
        ]
      }
    },
    created: function(){
      this.timelineMode = this.$store.state.timeline_mode || 'day';
    },
    watch: {
      timelineMode: function(data){
        this.$store.dispatch('switchTimelineMode', data);
      }
    }
  }
</script>




