<template>
  <div class="calendar">
    <header>
      <h2>{{monthTitle}} <span>{{currentYear}}</span></h2>
      <button @click="goPrevious" class="btn-prev fontawesome-angle-left"><i class="fa fa-chevron-left"></i></button>
      <button @click="goNext" class="btn-next fontawesome-angle-right"><i class="fa fa-chevron-right"></i></button>
    </header>
    <table>
      <thead>
      <tr>
        <td v-for="value in days">
          {{value}}
        </td>
      </tr>
      </thead>
      <tbody>
      <!--class: event, prev-month, current-day,next-month -->
      <tr>
        <td class="prev-month" v-for="prev in daysInPreviousMonth"
            v-if="prev && prev > daysInPreviousMonth - firstDayInCurrentMonth">{{prev}}</td>
        <td class="current-day" v-for="current in daysInCurrentMonth ">{{current}}</td>
        <td class="next-month" v-for="next in daysInNextMonth"
          v-if="next && next < 7 - lastDayInCurrentMonth">{{next}}</td>
      </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
  import fecha from 'fecha';

  var daysLabels = {
    sun: 'Sun',
    mon: 'Mon',
    tue: 'Tue',
    wed: 'Wed',
    thu: 'Thu',
    fri: 'Fri',
    sat: 'Sat'
  };

  var daysInMonth = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];

  var monthLabels = [
    "January",
    "February",
    "March",
    "April",
    "May",
    "June",
    "July",
    "August",
    "September",
    "October",
    "November",
    "December"
  ];

  export default {
   data: function() {
       return {
         days: daysLabels,
         currentDay: false,
         currentMonth: false,
         currentYear: false,
         currentDate: false,
         daysInCurrentMonth: 0,
         firstDayInCurrentMonth: false,
         lastDayInCurrentMonth: false,
         daysInPreviousMonth: 0,
         daysInNextMonth: 0
       }
    },
    created: function(){
      const routeDate = new Date(this.$store.state.route.params.date);
      this.currentDate = routeDate;
    },
    computed: {
      currentDay: function(){
        return this.currentDate.getDay();
      },
      currentMonth: function(){
        return this.currentDate.getMonth();
      },
      currentYear: function(){
        return this.currentDate.getFullYear();
      },
      daysInCurrentMonth: function(){
        return daysInMonth[this.currentMonth]
      },
      daysInPreviousMonth: function(){
        return daysInMonth[this.currentMonth === 0 ? 11 : this.currentMonth - 1]
      },
      daysInNextMonth: function(){
        return daysInMonth[this.currentMonth === 11 ? 0 : this.currentMonth + 1]
      },
      firstDayInCurrentMonth: function(){
        return new Date(this.currentYear, this.currentMonth, 1).getDay();
      },
      lastDayInCurrentMonth: function(){
        return new Date(this.currentYear, this.currentMonth, this.daysInCurrentMonth).getDay();
      },
      monthTitle: function(){
        return fecha.format(this.currentDate, 'MMMM'); // Month
      }
    },
    methods: {
      goPrevious: function() {
        this.currentDate = new Date(this.currentYear, this.currentMonth - 1, 1);
      },
      goNext: function() {
        this.currentDate = new Date(this.currentYear, this.currentMonth + 1, 1);
      }
    }
  }


</script>

<style>
  @import "variables.css";

  .calendar {
    transition: all .3s ease;
    text-align: center;
    overflow: hidden;
    margin:0 0 2rem;
  }

  .calendar i {
    font-size: 1.8rem;
    padding-top: 3px;
    color: #16407d
  }

  .calendar header {
    position: relative
  }

  .calendar header button {
    border: 0;
    background: #fff;
  }

  .calendar h2 {
    font-size: 2.7rem;
    margin-bottom: 0;
  }

  .calendar h2 span {
    font-weight: normal
  }

  .calendar tbody {
    color: #7c8a95;
  }

  .calendar tbody td:hover {
    border: 2px solid #16407d
  }

  .calendar table {
    margin-left: 3.7rem;
    border-collapse: collapse;
    border-spacing: 0
  }

  .calendar td {
    border: 2px solid transparent;
    display: inline-block;
    height: 2.2em;
    line-height: 2.2em;
    text-align: center;
    width: 2.5em;
    font-size: 14px;
    color: #000
  }

  .calendar thead td {
    font-weight: normal;
    text-transform: uppercase;
    font-size: 11px;
    color: #5e6066;
    width: 3.5rem
  }

  .calendar .event {
    cursor: pointer;
    position: relative;
  }

  .calendar .event:after {
    background: #16407d;
    border-radius: 50%;
    bottom: 1px;
    display: block;
    content: '';
    height: 6px;
    left: 50%;
    margin: -.25em 0 0 -.25em;
    position: absolute;
    width: 6px
  }

  .calendar .event.current-day:after {
    background: #e73e42
  }

  .calendar .btn-prev {
    left: 4rem
  }

  .calendar .btn-next {
    right: 4.1rem
  }

  .calendar .switch {
    text-align: left;
    color: #16407d;
    margin-left: -1rem;
    margin-top: 1rem;
  }

  .calendar .switch a {
    color: #16407d !important;
    font-size: 1.3rem
  }

  .calendar .prev-month, .calendar .next-month {
    color: #ddd
  }

  .calendar .prev-month:hover, .calendar .next-month:hover {
    border: 2px solid transparent
  }

  .calendar .btn-prev, .calendar .btn-next {
    text-decoration: none;
    height: 2em;
    font-size: .75em;
    line-height: 2em;
    margin: -1em;
    position: absolute;
    top: 43%;
    width: 2em;
    color: #16407d
  }

  .calendar .btn-prev:hover, .calendar .btn-next:hover {
    color: #000
  }

  .calendar table {
    text-align: left;
  }

  @media (--timeline-small) {
    .calendar {
      transition: none;
      border: 0;
      z-index: 1001;
      width: 100%;
      left: 0;
      padding: 0;
    }
    .calendar table {
      margin-left: 0;
      width: 100%;
    }
    .calendar thead td,
    .calendar td {
      width: 10%;
      font-size: 1.5rem;
      padding: 2% 0;
    }
    .calendar h2 {
      margin-bottom: 3rem;
    }
  }

  @media (--phone) {
    .calendar thead td,
    .calendar td {
      padding: 0;
    }
  }
</style>



