<template>
  <div class="event-separator">
    <div class="event-separator__header">
      <timeline-section-header v-bind:date="event.date"></timeline-section-header>
    </div>
  </div>
</template>

<style>
  @import "variables.css";

  .event-separator {
    margin: 0;
    padding: 0 0 0 5rem;
    width:1px;
  }

  @media (--timeline-large) {
    .event-separator {
      height:calc(100vh - var(--headerHeightLg));
    }
    .event-separator__header {
      margin-top: -5.5rem;
    }
  }

  @media (--header-medium) {
    .event-separator {
      height:calc(100vh - var(--headerHeightMd));
    }
  }

  @media (--timeline-small) {
    .event-separator {
      height:auto;
      width:100%;
      margin: 1rem 0 1rem -2.7rem;
      padding: 0;
    }
  }

</style>

<script>
  import TimelineSectionHeader from '../components/TimelineSectionHeader'


  export default {
    components: {
      TimelineSectionHeader
    },
    props: ['event']
  }
</script>




