<template>
  <div>
    <input type="checkbox" class="filter-toggle-checkbox" id="filterToggle">
    <label class="filter-toggle" for="filterToggle"><i class="icon-sliders"></i></label>
    <event-filter-list></event-filter-list>
  </div>
</template>

<style>
  @import "variables.css";

  .filter-toggle-checkbox {
    display: none;
  }
  .filter-toggle {
    width: var(--headerHeightLg);
    height: var(--headerHeightLg);
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;
    display: none;
  }

  .filter-toggle i {
    display: inline-block;
    width: var(--headerHeightLg);
    height: var(--headerHeightLg);
    line-height: 4rem;
    text-align: center;
    font-size: 2.4rem;
    color: #E9BE29;
    &:before {
       margin: 0;
       font-size: 1em;
     }
  }

  @media (--header-small) {
    .filter-toggle {
      display: block;
    }

    .filter-toggle-checkbox:checked + .filter-toggle i:before {
      content: '\e802';
    }
  }
</style>

<script>
  import EventFilterList from './EventFilterList'

  export default {
    components: {
      EventFilterList
    }
  }
</script>



