<template>
  <div>
    <input type="checkbox" name="toggle-calendar" id="toggleCalendar" class="filter-toggle-checkbox">
    <label for="toggleCalendar"><i class="icon icon-calendar"></i></label>
    <div class="calendar-view">
      <div class="calendar-wrapper">
        <calendar></calendar>
        <div class="switch">
          <footer-calendar-switch></footer-calendar-switch>
        </div>
        <div class="period">
          <footer-calendar-period></footer-calendar-period>
        </div>
      </div>
    </div>
  </div>
</template>

<style>
  @import "variables.css";

  #toggleCalendar:checked {
    display: none;
  }
  #toggleCalendar:checked + label + .calendar-view {
    display: block;
  }
  .filter-toggle-checkbox {
    display: none;
  }
  #toggleCalendar:checked + label > i.icon-calendar:before {
    content: '\E802';
  }
  #toggleCalendar:checked + label > .icon.icon-calendar {
    border-color:#fff;
    border-top-color: #16407d;
    left: 35rem;
  }
  .calendar-view {
    display: none;
    position: absolute;
    top:1rem;
    z-index: 899;
  }

  .icon-calendar {
    position: relative;
    z-index:900;
  }

  .calendar-wrapper {
    transition: all .3s ease;
    text-align: center;
    width: 347px;
    padding: 1rem 2rem 1rem;
    background: #fff;
    border: 2px solid #16407d;
    overflow: hidden;
  }

  .calendar-wrapper .switch button {
    background: none;
    border: 0;
    color: #16407d !important;
    font-size: 1.3rem;
    text-decoration: underline;
    float: left;
  }

  .calendar-wrapper .period {
    float: right;
  }

  .calendar-wrapper > select {
    border: 1px solid #16407d;
    font-size: 1.3rem;
  }

  @media (--timeline-small) {
    .calendar-view {
      width:100%;
      background: #fff;
      top:0;
    }
    .calendar-wrapper {
      border: none;
      margin: auto;
    }
    #toggleCalendar:checked + label > .icon.icon-calendar {
      position: absolute;
      top:0;
      right:0;
      left: auto;
      border-top-color: #fff;
      margin-right: 0;
    }

    #toggleCalendar:checked + label + .calendar-view {
      background: #fff;
      height: 100%;
    }
  }
</style>

<script>
  import Calendar from './Calendar'
  import FooterCalendarPeriod from './FooterCalendarPeriod'
  import FooterCalendarSwitch from './FooterCalendarSwitch'

  export default {
    components: {
      Calendar,
      FooterCalendarSwitch,
      FooterCalendarPeriod
    }
  }
</script>




