import 'whatwg-fetch';
import EventFakeClass from './data-generator'

const realBackendHost = 'http://stage.cc-calendar.com/calendar/api/v1/';

function OrganizationClass(backendHost) {
  var organizationClass = {};
  var host = backendHost;
  var loadedOrganizations;

  function getOrganizations() {
    if (!loadedOrganizations) {
      loadedOrganizations = loadOrganizations();
      return loadedOrganizations;
    }

    return getCachedOrganizations();
  }

  function loadOrganizations() {
    return fetch(host + 'organizations')
      .then(function (response) {
        return response.json();
      })
      .then(function (data) {
        return data.data;
      })
      .then(function (data) {
        setCachedOrganizations(data);
        return data;
      }).catch(function (ex) {
        console.log('parsing failed', ex);
      });
  }

  function setCachedOrganizations(data) {
    loadedOrganizations = data;
  }

  function getCachedOrganizations() {
    return Promise.resolve(loadedOrganizations);
  }

  organizationClass.getAll = function () {
    return getOrganizations();
  };

  organizationClass.getById = function (id) {
    return getOrganizations().then(function (data) {
      return _.find(data, {id: _.parseInt(id)});
    });
  };

  return organizationClass;
}

