function EventClass(backendHost) {
  var eventClass = {};
  var self = this;
  var host = backendHost;
  var loadedEvents;

  var defaultDateStart = new Date();
  defaultDateStart.setDate(defaultDateStart.getDate() - 1);
  defaultDateStart.setHours(0, 0, 0, 0);

  var defaultDateEnd = new Date();
  defaultDateEnd.setDate(defaultDateEnd.getDate() + 1);
  defaultDateEnd.setHours(23, 59, 59, 999);

  self.date_start = defaultDateStart;
  self.date_end = defaultDateEnd;

  var eventTitles = {
    'treningssamlinger': 'Trenings\u00ADsamlinger',
    "pressekonferanser":'Presse\u00ADkonferanser',
    'uci kongress':'Uci\u00AD kongress'
  };

  function eventHyhenator(v) {
    v.category.name = eventTitles[v.category.name.toLowerCase()] || v.category.name;
    return v;
  }

  function setEventPages(event) {

    var ts = new Date().getTime();

    return fetch(host + 'events/' + event.id + '/pages?rand=' + ts)
      .then(function (response) {
        return response.json();
      })
      .then(function (data) {
        return data.data;
      })
      .then(function (data) {
        event.pages = _.sortBy(data, 'order_number');
        return event;
      })
      .catch(function (ex) {
        console.log.bind(console);
        console.log('parsing failed', ex);
      });
  }

  function setEventTheme(v) {
    v.event_theme = v.category.parent_id == 3 ? 'grey-blue' : (v.category.parent_id == 2 ? 'blue-red' : 'grey-black');
    return v;
  }

  function getEvents() {
    if (!loadedEvents) {
      loadedEvents = loadEvents();
      return loadedEvents;
    }

    return getCachedEvents();
  }

  function loadEvents() {
    var ts = new Date().getTime();

    var periodStart = self.date_start.getTime() - msInDay;
    var periodEnd = self.date_end.getTime() + msInDay;

    return fetch(host + 'events/find?event_start=' + periodStart + '&event_start=' + periodEnd + '&rand=' + ts)
      .then(function (response) {
        return response.json();
      })
      .then(function (data) {
        //return only inner data object
        return data.data;
      })
      .then(filterByPeriod)
      .then(function (data) {
        return _.chain(data).map(eventHyhenator).map(setEventTheme).value();
      })
      .then(setCachedEvents)
      .catch(function (ex) {
        console.log('parsing failed', ex);
      });
  }

  function filterByPeriod(data) {
    return _.filter(data, function (val) {
      if (+val.start_date >= +self.date_start.getTime() &&
        +val.start_date <= +self.date_end.getTime()) {
        return val;
      }
    });
  }

  function setCachedEvents(data) {
    loadedEvents = data;
    return loadedEvents;
  }

  function getCachedEvents() {
    return Promise.resolve(loadedEvents);
  }

  eventClass.setEventsPeriod = function (date_start, date_end) {
    self.date_start = date_start;
    self.date_end = date_end;
    loadedEvents = undefined;

    return eventClass;
  };

  eventClass.getAll = function () {
    return getEvents();
  };

  eventClass.getById = function (id) {
    return getEvents().then(function (data) {
      var event = _.find(data, {id: _.parseInt(id)});

      if (event) {
        return setEventPages(event);
      } else {
        return fetch(host + 'events/' + _.parseInt(id))
          .then(function (response) {
            return response.json();
          })
          .then(function (data) {
            if (data) {
              return data.data;
            } else {
              var error = new Error('Event not exist');
              throw error;
            }
          })
          .then(eventHyhenator)
          .then(setEventTheme)
          .then(setEventPages)
          .catch(function (ex) {
            console.log('parsing failed', ex);
          });
      }
    });
  };

  //These fields were provided by BackEnd developer
  //event {
  //    body (string, optional),
  //        category (category, optional),
  //        category_id (integer, optional),
  //        end_date (Timestamp, optional),
  //        event_level (EventLevel, optional),
  //        event_level_id (integer, optional),
  //        event_region (EventRegion, optional),
  //        event_region_id (integer, optional),
  //        id (integer, optional),
  //        latitude (number, optional),
  //        longitude (number, optional),
  //        organization (Organization, optional),
  //        organization_id (integer, optional),
  //        position_description (string, optional),
  //        start_date (Timestamp, optional),
  //        title (string, optional)
  //}

  eventClass.add = function (params) {
    var token = User.getAuthToken();

    return fetch(host + 'events?token=' + token, {
      method: 'post',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(
        {
          title: params.title,
          body: params.body,
          end_date: params.end_date,
          start_date: params.start_date,

          //should be int
          organization_id: params.organization_id,
          category_id: params.category_id,
          event_region_id: params.event_region_id,
          event_level_id: params.event_level_id,

          //add additional  params later

          latitude: params.latitude,
          longitude: params.longitude,
          map_zoom: params.map_zoom,
          position_description: params.position_description
        }
      )
    }).then(function (response) {
      if (response.status >= 200 && response.status < 300) {

        return response.json();

        //    todo:
        //    sometimes back end returns data.data
        //    maybe it is better for us to handle it at request
        //    return data.data
        //    in order to return only relevant response, not nested

      } else {
        var error = new Error(response.statusText);
        error.response = response;
        throw error;
      }
    });
  };

  eventClass.update = function (id, params) {
    var token = User.getAuthToken();

    return fetch(host + 'events/' + parseInt(id) + '?token=' + token, {
      method: 'put',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(
        {
          title: params.title,
          body: params.body,

          end_date: params.end_date,
          start_date: params.start_date,

          //should be int
          organization_id: params.organization_id,
          category_id: params.category_id,
          event_region_id: params.event_region_id,
          event_level_id: params.event_level_id,

          latitude: params.latitude,
          longitude: params.longitude,
          map_zoom: params.map_zoom,
          position_description: params.position_description
        }
      )
    }).then(function (response) {
      if (response.status >= 200 && response.status < 300) {
        return response.json();
      } else {
        var error = new Error(response.statusText);
        error.response = response;
        throw error;
      }
    });
  };

  eventClass.delete = function (eventId) {
    var token = User.getAuthToken();
    return fetch(host + 'events' + '?token=' + token, {
      method: 'delete',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(
        [parseInt(eventId)]
      )
    }).then(function (response) {
      if (response.status >= 200 && response.status < 300) {
        return response;
      } else {
        var error = new Error(response.statusText);
        error.response = response;
        throw error;
      }
    });
  };

  eventClass.addPages = function (eventId, pages) {
    var token = User.getAuthToken();

    return fetch(host + 'events/' + eventId + '/pages?token=' + token, {
      method: 'post',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json'
      },

      //array
      body: JSON.stringify(pages)
    }).then(function (response) {
      if (response.status >= 200 && response.status < 300) {
        return response;
      } else {
        var error = new Error(response.statusText);
        error.response = response;
        throw error;
      }
    });

  };

  //todo, method could be removed
  eventClass.updatePages = function (eventId, pages) {
    return fetch(host + 'event/' + eventId + 'pages/' + pageId + '?token=' + token, {
      method: 'put',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(pages)
    }).then(function (response) {
      if (response.status >= 200 && response.status < 300) {
        return response;
      } else {
        var error = new Error(response.statusText);
        error.response = response;
        throw error;
      }
    });
  };

  eventClass.deletePages = function (pages) {
    var token = User.getAuthToken();

    return fetch(host + 'event/pages?token=' + token, {
      method: 'delete',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(pages)
    }).then(function (response) {
      if (response.status >= 200 && response.status < 300) {
        return response;
      } else {
        var error = new Error(response.statusText);
        error.response = response;
        throw error;
      }
    });
  };

  return eventClass;
}

const Organizations = new OrganizationClass(realBackendHost);
const Levels = new LevelClass(realBackendHost);
const Regions = new RegionClass(realBackendHost);
const Categories = new CategoryClass(realBackendHost);
// const Events = new EventClass(realBackendHost);
const Events = new EventFakeClass(Categories, Regions, Levels, Organizations);

export default {Organizations, Levels, Regions, Categories, Events}




